.input-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem; /* Отступ снизу */
  width: 100%; /* По умолчанию занимает всю доступную ширину */
  max-width: 400px; /* Ограничим максимальную ширину для демонстрации */
  position: relative; /* Для позиционирования outline лейбла */
}

/* --- Стили для Standard Label (variant='standard') --- */

/* Стиль для стандартного лейбла (над полем) */
.input-label {
  margin-bottom: 0.3rem;
  font-size: 0.9rem;
  color: #333;
  font-weight: 500;
  transition: color 0.2s ease-in-out;
}

/* Затемнение стандартного лейбла при ошибке */
.input-container--error .input-label {
    color: #dc3545;
}

/* --- Стили для Outline Label (variant='outline') --- */

/* Убираем стандартный label для outline варианта */
.input-container--label-outline .input-label {
    display: none;
}

/* Стили для fieldset, который будет рамкой */
.input-fieldset {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 0; /* Убираем стандартный padding */
  margin: 0; /* Убираем стандартный margin */
  transition: border-color 0.2s ease-in-out;
  position: relative; /* Для корректной работы legend */
}

/* Стили для legend, содержащего лейбл */
.input-legend {
  padding: 0 4px; /* Небольшой отступ по бокам от текста */
  margin-left: 8px; /* Сдвигаем вправо от края */
  font-size: 0.75rem; /* Уменьшаем шрифт лейбла в рамке */
  color: #6c757d; /* Серый цвет для неактивного лейбла */
  transition: color 0.2s ease-in-out, max-width 0.2s ease-in-out;
  max-width: 0.01px; /* Схлопываем по умолчанию */
  visibility: hidden; /* Скрываем по умолчанию */
  white-space: nowrap;
  display: block; /* Важно для правильного схлопывания */
}
/* Дополнительная обертка для правильного выравнивания и анимации */
.input-legend span {
    display: inline-block;
    padding-left: 5px;
    padding-right: 5px;
}


/* Анимация и показ legend при фокусе или наличии значения/плейсхолдера */
/* Используем :focus-within на fieldset и псевдокласс :not(:placeholder-shown) на input */
.input-fieldset:focus-within .input-legend,
.input-field:not(:placeholder-shown) + .input-legend, /* Для React нужно проверить, сработает ли с fieldset */
.input-container--label-outline .input-field:not(:placeholder-shown) ~ .input-legend /* Корректный селектор для нашей структуры */
{
  max-width: 100%;
  visibility: visible;
  color: #007bff; /* Цвет активного лейбла (при фокусе) */
}

/* Стили fieldset при фокусе */
.input-fieldset:focus-within {
  border-color: #007bff;
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25); /* Тень имитируем на fieldset */
}

/* Стили fieldset при ошибке */
.input-container--error .input-fieldset {
  border-color: #dc3545;
}
/* Стили legend при ошибке и фокусе/значении */
.input-container--error .input-fieldset:focus-within .input-legend,
.input-container--error .input-field:not(:placeholder-shown) ~ .input-legend {
    color: #dc3545;
}

/* Стили fieldset при ошибке и фокусе */
.input-container--error .input-fieldset:focus-within {
  box-shadow: 0 0 0 2px rgba(220, 53, 69, 0.25);
}


/* --- Общие стили для Input Field --- */

.input-field {
  padding: 0.8rem 0.8rem 0.4rem; /* Немного изменим паддинги для outline */
  border: none; /* Убираем стандартную рамку инпута */
  border-radius: 0; /* Обнуляем */
  font-size: 1rem;
  transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  width: 100%;
  box-sizing: border-box;
  background: none; /* Убираем фон */
  outline: none; /* Убираем стандартный outline */
}

/* Убираем box-shadow с инпута при фокусе, т.к. он на fieldset */
.input-field:focus {
  box-shadow: none;
}

/* Стили инпута при ошибке (только цвет текста, рамка на fieldset) */
.input-field--error {
   /* border-color убран, он на fieldset */
}

/* Убираем box-shadow с инпута при ошибке и фокусе */
.input-field--error:focus {
  box-shadow: none;
}

/* Стиль для сообщения об ошибке (без изменений) */
.input-error-message {
  margin-top: 0.3rem;
  font-size: 0.8rem;
  color: #dc3545;
} `, "",{"version":3,"sources":["webpack://./src/components/Input/Input.css"],"names":[],"mappings":"AAAA,0CAA0C;AAC1C;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB,EAAE,iBAAiB;EACtC,WAAW,EAAE,+CAA+C;EAC5D,gBAAgB,EAAE,mDAAmD;EACrE,kBAAkB,EAAE,wCAAwC;AAC9D;;AAEA,0DAA0D;;AAE1D,8CAA8C;AAC9C;EACE,qBAAqB;EACrB,iBAAiB;EACjB,WAAW;EACX,gBAAgB;EAChB,kCAAkC;AACpC;;AAEA,8CAA8C;AAC9C;IACI,cAAc;AAClB;;AAEA,wDAAwD;;AAExD,mDAAmD;AACnD;IACI,aAAa;AACjB;;AAEA,6CAA6C;AAC7C;EACE,sBAAsB;EACtB,kBAAkB;EAClB,UAAU,EAAE,gCAAgC;EAC5C,SAAS,EAAE,+BAA+B;EAC1C,yCAAyC;EACzC,kBAAkB,EAAE,iCAAiC;AACvD;;AAEA,wCAAwC;AACxC;EACE,cAAc,EAAE,wCAAwC;EACxD,gBAAgB,EAAE,4BAA4B;EAC9C,kBAAkB,EAAE,mCAAmC;EACvD,cAAc,EAAE,sCAAsC;EACtD,8DAA8D;EAC9D,iBAAiB,EAAE,4BAA4B;EAC/C,kBAAkB,EAAE,0BAA0B;EAC9C,mBAAmB;EACnB,cAAc,EAAE,sCAAsC;AACxD;AACA,mEAAmE;AACnE;IACI,qBAAqB;IACrB,iBAAiB;IACjB,kBAAkB;AACtB;;;AAGA,yEAAyE;AACzE,yFAAyF;AACzF;;;;EAIE,eAAe;EACf,mBAAmB;EACnB,cAAc,EAAE,uCAAuC;AACzD;;AAEA,8BAA8B;AAC9B;EACE,qBAAqB;EACrB,6CAA6C,EAAE,+BAA+B;AAChF;;AAEA,8BAA8B;AAC9B;EACE,qBAAqB;AACvB;AACA,8CAA8C;AAC9C;;IAEI,cAAc;AAClB;;AAEA,uCAAuC;AACvC;EACE,6CAA6C;AAC/C;;;AAGA,wCAAwC;;AAExC;EACE,6BAA6B,EAAE,yCAAyC;EACxE,YAAY,EAAE,qCAAqC;EACnD,gBAAgB,EAAE,aAAa;EAC/B,eAAe;EACf,sEAAsE;EACtE,WAAW;EACX,sBAAsB;EACtB,gBAAgB,EAAE,gBAAgB;EAClC,aAAa,EAAE,gCAAgC;AACjD;;AAEA,gEAAgE;AAChE;EACE,gBAAgB;AAClB;;AAEA,oEAAoE;AACpE;GACG,uCAAuC;AAC1C;;AAEA,oDAAoD;AACpD;EACE,gBAAgB;AAClB;;AAEA,kDAAkD;AAClD;EACE,kBAAkB;EAClB,iBAAiB;EACjB,cAAc;AAChB","sourcesContent":["/* Контейнер для инпута, лейбла и ошибки */\n.input-container {\n  display: flex;\n  flex-direction: column;\n  margin-bottom: 1rem; /* Отступ снизу */\n  width: 100%; /* По умолчанию занимает всю доступную ширину */\n  max-width: 400px; /* Ограничим максимальную ширину для демонстрации */\n  position: relative; /* Для позиционирования outline лейбла */\n}\n\n/* --- Стили для Standard Label (variant='standard') --- */\n\n/* Стиль для стандартного лейбла (над полем) */\n.input-label {\n  margin-bottom: 0.3rem;\